
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: var(--transition-standard);
  scroll-behavior: smooth;
}

:root {
  --bg-color: #1f242d;
  --second-bg-color: #323946;
  --text-color: #fff;
  --main-color: #0ef;
  --padding-standard: 10px;
  --transition-standard: 0.3s;
}

body {
  background: var(--bg-color);
  padding: var(--padding-standard);
  color: var(--text-color);
}

header,
nav,
section {
  padding: var(--padding-standard);
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2rem 9%;
  background: var(--bg-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
}

header.sticky {
  border-bottom: 0.1rem solid rgba(0, 0, 0, 0.2);
}

.logo {
  font-size: 29px;
  color: var(--text-color);
  font-weight: 700;
  cursor: default;
}

.navbar a {
  font-size: 28px;
  color: var(--text-color);
  margin-left: 4rem;
  transition: 0.3s;
}

.navbar a:hover,
.navbar a:active {
  color: var(--main-color);
}

#menu-icon {
  font-size: 18px;
  color: var(--text-color);
  display: none;
}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-img img {
  width: 35vw;
}

span {
  color: var(--main-color);
}

.home-content h1 {
  font-size: 48px;
  font-weight: 700;
  line-height: 1.3;
}

.home-content p {
  font-size: 24px;
  margin: 2rem 0;
  padding: auto;
}

.social a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  background: transparent;
  border: 0.2rem solid var(--main-color);
  border-radius: 50%;
  font-size: 26px;
  color: var(--main-color);
  margin: 3rem 1.5rem 3rem 0;
  transition: 0.5s ease;
}

.social a:hover {
  background: var(--main-color);
  color: var(--second-bg-color);
  box-shadow: 0 0 1rem var(--main-color);
}

.btn {
  display: inline-block;
  padding: 1rem 2.8rem;
  background: var(--main-color);
  border-radius: 4rem;
  box-shadow: 0 0 1rem var(--main-color);
  font-size: 14px;
  color: var(--second-bg-color);
  letter-spacing: 0.1rem;
  font-weight: 600;
  transition: 0.5s;
}

.btn:hover {
  box-shadow: none;
}

.about {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background: var(--second-bg-color);
}

.about-img img {
  width: 35vw;
}

.heading {
  text-align: center;
  font-size: 28px;
}

.about-content h2 {
  text-align: left;
  line-height: 1.2;
}

.about-content h3 {
  font-size: 26px;
}

.about-content p {
  font-size: 14px;
  margin: 2rem 0 3rem;
}

.container-fluid {
  background: var(--second-bg-color);
}

.container-fluid h2 {
  margin-bottom: 4rem;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -1.5rem;
}

.col-lg-4,
.col-md-6,
.col-sm-12 {
  padding: 0 1.5rem;
}

.card {
  position: relative;
  border-radius: 2rem;
  box-shadow: 0 0 1rem var(--bg-color);
  overflow: hidden;
  display: flex;
  color: black;
}

.card-img-top {
  width: 100%;
  transition: 0.5s ease;
}

.card:hover .card-img-top {
  transform: scale(1.1);
}

.card-body {
  padding: 2rem;
}

.card-title {
  font-size: 3rem;
}

.card-text {
  font-size: 1.6rem;
  margin: 0.3rem 0 1rem;
}

.card-body a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  background: var(--text-color);
  border-radius: 50%;
}

.card-body a i {
  font-size: 16px;
  color: var(--second-bg-color);
}

.contact h2 {
  margin-bottom: 3rem;
}

.contact form {
  max-width: 70rem;
  margin: 1rem auto;
  text-align: center;
  margin-bottom: 3rem;
}

.contact form .form-group {
  margin-bottom: 1rem;
}

.contact form input[type="text"],
.contact form input[type="email"],
.contact form textarea {
  width: 100%;
  padding: 1.5rem;
  font-size: 14px;
  color: var(--text-color);
  background: var(--second-bg-color);
  border-radius: 0.8rem;
  margin: 0.7rem 0;
}

.contact form textarea {
  resize: none;
}

.contact form .btn {
  margin-top: 2rem;
  cursor: pointer;
}

@media (max-width: 1200px) {
  :root {
      --padding-standard: 5px;
  }
}

@media (max-width: 991px) {
  header {
      padding: 2rem 3%;
  }

  section {
      padding: 10rem 3% 2rem;
  }
}

@media (max-width: 768px) {

  nav.navbar {
      /* position: absolute; */
      /* top: 100%; */
      left: 0;
      width: 100%;
      /* padding: 1rem 3%; */
      background: var(--bg-color);
      border-top: 0.1rem solid rgba(0, 0, 0, 0.2);
      box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  }

  nav.navbar a {
      display: block;
      font-size: 16px;
      margin: 3rem 0;
  }
}